

import report from '@/utils/statistics'
import '@/common/css/common.css'
import '@/common/css/self.css'
import sfApi from '@/store/apis/sfFestival'

import { mapState, mapActions, mapMutations } from 'vuex'
import { session, local } from '@/utils/storage'
import Wechat from '@/utils/wechat'
import bdWin from '@/components/bdWin'
import shareWin from '@/components/shareWin'
import ImgLogo from '@/components/ImgLogo'
import ImgLeague from '@/components/ImgLeague'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'




export default {
directives: {
TransferDom
},
components: {
Divider,
XButton,
Confirm,
XDialog,
Loading,
Toast,
bdWin,
shareWin,
ImgLogo,
ImgLeague
},
data() {
return {
choose: -1, //选择了第几个品牌
sucNum: 3,
rightArr: [], //题目答对的数组

type: 0, //0开始界面，1//品牌列表 2//游戏界面
showWin: false,
winSuc: false,
showShareWin: false,
remain_count: 0, //剩余的答题机会次数
questionIndex: 0,
questionList: [], //题目

user_answer_id: null,
answer: [], //用户答案
brandList: [],

giftObj: {} 

}
},
beforeRouteLeave(to, from, next) {
next()
},
beforeDestroy() {},
methods: {
...mapActions({

}),
...mapMutations({

}),
start() { // 进入活动
if(this.remain_count <= 0) {
this.$toast('答题次数已经用完')
return;
}
this.type = 1

report('品牌答题落地页', '点击', '立刻挑战')
},
shareRP() {
var rpName = '未知';
var rpCat = '未知'
if(this.type == 0) {
rpCat = '品牌答题落地页'
rpName = '页面分享'
} else if(this.type == 1) {
rpCat = '品牌商选择'
rpName = '分享好友页面分享'
} else return;
report(rpCat, '次数', rpName)

},
chooseBrand(index) {
this.choose = index;
var brand = this.brandList[this.choose]
var name = '未知'
if(brand) name = brand.name;

report('品牌商选择', '点击', name + ' 品牌点击')
},
attend() { //参加活动
this.$router.push('/sfhome')
report('品牌答题落地页', '点击', '参与活动赢字卡按钮')
},
startGame() { //开始游戏
if(this.remain_count <= 0) {
this.$toast('答题次数已经用完')
return;
}

var brand = this.brandList[this.choose]
if(brand == null) {
this.$toast('请选择品牌')
return;
}

if(!brand.is_start) {
this.$toast('活动不可以参与')
return;
}

this.type = 2

sfApi.startAnswer(brand.id).then(resData => {
if(!resData.data) {
this.$toast('data is empty')
return;
}

var data = resData.data

if(!data.question) {
this.$toast('question is null')
return;
}

var questionList = data.question
this.questionList = questionList || [];


this.user_answer_id = data.user_answer_id






}).catch(this.$Err)
report('品牌商选择', '点击', '开始游戏')
},
chooseAnswer(item,qus) {

if(qus.type==0){//单选
this.answer=[]
}else {//多选

}


var index = this.answer.indexOf(item.key)
if(index >= 0) {
this.answer.splice(index, 1)
return;
}
this.answer.push(item.key)
},
showEndWin(data, cb) { //cb 为可以进入下一道题的回调
var is_correct = data.is_correct
is_correct = !!is_correct
this.rightArr.push(is_correct)
if(this.calStatus == 0) { //没有成功  //calStatus -1 可以继续答题，0 失败了 ，1 成功了
this.showWin = true
this.winSuc = false
} else if(this.calStatus == 1) { //跳转成功
if(data.gift != null) { // 有礼物返回
this.giftObj = data.gift
this.showWin = true
this.winSuc = true
} else {
this.showWin = true
this.winSuc = true
this.$toast('后台未提供礼物')
}
}

cb && cb(this.calStatus)

},
submit() { //提交答案
if(this.answer.length == 0) {
this.$toast('请选择答案')
return;
}

sfApi.submitAns({
user_answer_id: this.user_answer_id,
answer: this.answer,
index: this.questionIndex
}).then(data => {
this.answer = []

this.showEndWin(data.data, (status) => {

this.questionIndex++;
})
}).catch(this.$Err)
},
seePrise() { //win里面的查看

},
winTap(tab) {
var rpName = '未知'
if(tab == 0) {
if(this.remain_count > 0) {
this.restartGame()
} else {
this.$toast('今日次数用完，请分享获得额外次数。')
}
rpName = '继续挑战'
} else if(tab == 1) {
this.$router.push('/sfhome')
rpName = '更多活动'
} else if(tab == 2) {
this.showShareWin = true
rpName = '分享好友'
this.refreshShare()
}
report('品牌商选择', '点击', rpName)
},
closeShareWin() {

},
loadBandList() {
sfApi.sfInfo().then(data => {
this.remain_count = data.data.remain_answer_count * 1
if(isNaN(this.remain_count)) this.remain_count = 0;

return sfApi.getBrandList();
}).then(data => {
if(!data.data) return;
this.brandList = data.data
}).catch(this.$Err)
},
getWxConfig(cb) {
if(this.wxShareCB == null) this.wxShareCB = cb;
this.refreshShare()

},
restartGame() {
this.questionList = []
this.type = 0
this.questionIndex = 0
this.showWin = false
this.showShareWin = false
this.user_answer_id = null
this.brandList = []
this.answer = []
this.giftObj = {}
this.rightArr = []
this.loadBandList()
this.refreshShare()
},

refreshShare() {
var obj = {
link: location.href,
title: '元宵节答题挑战，答对3道就是天才！',
desc: '我连续答对了3道题，你敢挑战吗？'
}
if(this.showShareWin) { //分享的图层打开，
obj.link.origin + '/sfcard'
obj.title = '和+邀您一起集字卡，集齐100%中大奖！'
obj.desc = '送你一张字卡'
}

Wechat.initShare(obj, this.wxShareCB)
},

},
created() {
this.getWxConfig(() => {
this.shareRP()
if(this.showShareWin) { //有蒙层的分享才会继续游戏
sfApi.answerActiveShare().then((data) => {
this.restartGame()
}).catch(this.$Err);
}

})

this.loadBandList()

},
mounted() {

},
computed: {
...mapState({
myInfo: state => state.user.myInfo,
mySource: state => state.user.mySource
}),
calStatus() {
var status = -1;


var numMeet = this.rightArr.filter((isRight) => {
return isRight;
}).length >= this.sucNum;

if(numMeet) status = 1; 

if(this.questionIndex >= this.questionList.length - 1) { //最后一道题了且没有满足条件
if(!numMeet) status = 0; 
}

return status;

},

question() {
var obj = this.questionList[this.questionIndex]
if(obj == null) obj = {}

return obj;
},
calBg() {
var name = 'bg.png'
if(this.type == 1) name = 'bg1.png'
if(this.type == 2) name = 'bg2.png'
return name;
},
calRewardObj() {
var obj = {
type: 0
}
var data = this.giftObj
if(data.data == null) return obj;

if(data.giftType == 0) { //卡片
var cardType = data.data.cardType * 1 + 1
if(isNaN(cardType)) {
console.error('cardType is NaN')
cardType = 0;
}
if(cardType == 5) cardType = 0;

var name = '';
if(cardType == 0) name = 'H.png';
if(cardType == 1) name = 'P.png';
if(cardType == 2) name = 'L.png';
if(cardType == 3) name = 'U.png';
if(cardType == 4) name = 'S.png';

obj.url = require('@/assets/sfImg/home/' + name)
} else { //优惠券
obj.url = data.data.imageUrl
obj.type = 1
}
return obj;
}
},

}
